import React from 'react';
import '../App.css';
import './nav.css';
import { Link } from 'react-router-dom';

function Nav() {

  return (
    <div className="Nav">
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <div className="container-fluid nav-links">
          <Link to='/' className="navbar-brand logo" href="/index.html">Recipe Storage</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mb-2 mb-lg-0">
              <li><Link to='/add-item' className="nav-link"> Add Item </Link></li>
              <li><Link to='/view-items' className="nav-link"> View Items </Link></li>
              <li><Link to='/view-recipes' className="nav-link"> View Recipes </Link></li>
              <li><Link to='/login' className="nav-link"> Login </Link></li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Nav;
