import React, {useState} from 'react';
import { Link } from 'react-router-dom';

const Login = props => {
  const [hasAccount, setHasAccount] = useState(false);

  return (
    <div className="main-container">
      <h1>Recipe Storage</h1>
      <UserInput hasAccount={!hasAccount} />
      <div className="login-container">
        <Link to="/add-item" className="button-link">
          <button>
            {hasAccount && "Login"}
            {!hasAccount && "Create"}
          </button>
        </Link>
        <a className="link" onClick={() => setHasAccount(!hasAccount)}>
          {hasAccount && "Don't have an account? Click here to create one."}
          {!hasAccount && "Have an account? Click here to login."}
        </a>
      </div>
      <div className="extra-space"></div>
    </div>
  );
}

export default Login;

const UserInput = props => {
  const { hasAccount } = props;

  return (
    <div className="user-input">
      {hasAccount &&
        <div>
          <label className="item" htmlFor="first-name">First Name:</label>
          <form className="item">
            <input type="text" name="first-name"></input>
          </form>
          <label className="item" htmlFor="last-name">Last Name:</label>
          <form className="item">
            <input type="text" name="last-name"></input>
          </form>
        </div>
      }
      <label className="item" htmlFor="username">Username:</label>
      <form className="item">
        <input type="email" name="username"></input>
      </form>
      <label className="item" htmlFor="password">Password:</label>
      <form className="item">
        <input type="password" name="password"></input>
      </form>
      {hasAccount &&
        <div>
          <label className="item" htmlFor="password">Confirm Password:</label>
          <form className="item">
            <input type="password" name="password"></input>
          </form>
        </div>
      }
    </div>
  );
};
